import { assign } from 'lodash';

import { Constructor } from '@helix/platform/utils';

export function RxGetRecordServerActionMixin<TBase extends Constructor>(Base: TBase) {
  return class RxGetRecordServerAction extends Base {
    initialize(config: any): void {
      // @ts-ignore
      super.initialize(config);
    }

    afterRecordDefinitionNameChange(inputMapPropertyValue: string): void {
      // @ts-ignore
      this.lastRecordDefinitionName = inputMapPropertyValue;

      // @ts-ignore
      this.clearOutputMap();

      // @ts-ignore
      this.setInputMap(
        // @ts-ignore
        assign(this.getInputMap(), {
          // @ts-ignore
          recordDefinitionName: this.lastRecordDefinitionName,
          sampleRecordDefinitionName: null,
          attachmentFieldID: ''
        })
      );

      // @ts-ignore
      this.getCommandManager().storeBatchCommand();
    }

    afterSampleRecordDefinitionNameChange(inputMapPropertyValue: string): void {
      // @ts-ignore
      this.lastSampleRecordDefinitionName = inputMapPropertyValue;

      // @ts-ignore
      this.clearOutputMap();

      // @ts-ignore
      this.setInputMap(
        // @ts-ignore
        assign(this.getInputMap(), {
          // @ts-ignore
          recordDefinitionName: this.lastRecordDefinitionName,
          // @ts-ignore
          sampleRecordDefinitionName: this.lastSampleRecordDefinitionName
        })
      );

      // @ts-ignore
      this.getCommandManager().storeBatchCommand();
    }

    getRecordDefinitionNameChangeConfirmationMessageKey(): string {
      return 'com.bmc.arsys.rx.client.process-designer.inspector.clear-output-map-confirmation.message';
    }
  };
}
